@import "ScssHelpers/_helpers_all.scss";

.about_section {
	z-index: 0;
	padding-bottom: 0;

	@include bp($point_2, min) {
		padding-top: 3.6rem;
		padding-bottom: 8.4rem;
	}

	@include bp($point_3, $point_7) {
		padding-top: 1.8rem;
		padding-bottom: 3rem;
	}

	@include bp($point_7 - 1) {
		padding: 0 3rem;
	}
}

.about_section_in {
	position: relative;

	max-width: $page_2_width;
}

.about_section__text {
	@extend %text;

	position: relative;

	max-width: 104rem;

	p {
		&:nth-child(2) {
			font-weight: 400;
			letter-spacing: normal;
		}

		strong {
			@extend %text_strong;

			@include bp($point_2, min) {
				letter-spacing: 0.01em;
			}
		}

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 3.2rem;
			}

			@include bp($point_3) {
				margin-bottom: 2.4rem;
			}
		}
	}

	@include bp($point_2, min) {
		padding-left: 11.3rem;

		margin-bottom: 13rem;

		line-height: 1.32;
	}

	@include bp($point_3) {
		padding-bottom: 8.4rem;

		line-height: 1.33;
	}

	/*.body--safari_state & {
		line-height: 1.55;
	}*/
}

.about_section_content {
	position: relative;

	@include bp($point_2, min) {
		padding: 9.4rem 1rem 10rem 11.5rem;
		margin-right: -13.6rem;
	}

	@include bp($point_3) {
		padding: 10rem var(--gl-indent) 10.6rem;
		margin: 0 -7.3rem;
	}
}

.about_section_content__text {
	@extend %text;

	position: relative;

	max-width: 92.5rem;

	font-weight: 400;
	color: $white;

	strong {
		@extend %text_strong;
	}
}

// -------- decor bg---------

.about_section__bg {
	color: $white_2;

	@include bp($point_4, min) {
		z-index: 0;
		position: absolute;
		top: 0;
		left: var(--gl-indent);

		width: calc(100% - var(--gl-indent));
		height: 62rem;
		margin-top: -7rem;
	}

	@include bp($point_4 - 1) {
		display: none;
	}
}

.about_section__img {
	filter: blur(1.2rem);
}

.about__bg_svg {
	width: 100%;
	height: 100%;

	will-change: opacity;

	&.about__bg_svg--v2 {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;
	}
}

.about_section_content__decor {
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	height: 100%;
	width: 100%;

	will-change: opacity;

	&.about_section_content__decor--v2 {
		z-index: 2;
	}

	@include bp($point_3) {
		display: none;
	}
}

.about_section_content__decor_mob {
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	height: 100%;

	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		display: block;
	}
}

.about_section__img--2_mod {
	position: absolute;
	right: 0;
	bottom: 0;

	width: 33.2rem;
	height: 33.1rem;
	margin-right: -30rem;
}

.about_section_content__bg {
	position: absolute;
	top: 0;
	left: 0;

	height: 100%;
	width: 100%;

	// background: $gradient;
	overflow: hidden;

}

.about_section_content__gradient {
	position: absolute;
	top: 0;
	left: 0;

	height: 100%;
	width: 100vw;

	background: $gradient;
}

.about_section_content__img--1_mod,
.about_section_content__img--2_mod {
	@include bp($point_3 - 1) {
		display: none;
	}
}

.about_section_content__img--1_mod {
	@include bp($point_3, min) {
		margin-top: -1.8rem;
		margin-left: -2rem;

		filter: blur(1.2rem);
	}
}

.about_section_content__img--2_mod {
	@include bp($point_3, min) {
		position: absolute;
		top: 0;
		left: 0;

		width: 100%;
		height: 33.3rem;
		margin-left: -11.6rem;
	}
}

.about_section_content__img--3_mod,
.about_section_content__img--4_mod {
	@include bp($point_3, min) {
		display: none;
	}
}

.about_section_content__img--3_mod {
	@extend %cover_img;

	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

